import React, { useEffect, useState } from "react";
import { Link, withRouter } from "react-router-dom";
import { main, menu, profile, cart, choosen } from "./MobileBottomNavbarData";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import { checkPathAndActivateMenu } from "./MobileBottomNavbar";

function openMenu(e) {
	e.preventDefault();
	const body = document.querySelector("body");
	body.classList.add("mmenu-active");
}

const MobileBottomNavbarThree = ({ location }) => {
	const { token } = useSelector((state) => state.user.tokenData);
	const [active, setActive] = useState(1);
	const { t } = useTranslation();

	useEffect(() => {
		checkPathAndActivateMenu(location.pathname, setActive);
	}, [location.pathname]);

	return (
		<div className="mobile_btm_navbar_box mobile_btm_navbar_box-three">
			<ul>
				<Link to={main.path} onClick={() => setActive(1)}>
					<li key={main.key}>
						<img
							src="/assets/images/homeplus/home.svg"
							alt="home icon"
							width="25"
							height="25"
						/>
						<span
							className={`${active === 1 ? "text-primary" : ""}`}
						>
							{t(main.key)}
						</span>
					</li>
				</Link>
				<Link to="" onClick={(e) => openMenu(e)}>
					<li key={menu.key}>
						<img
							src="/assets/images/homeplus/catalog.svg"
							alt="catalog icon"
							width="25"
							height="25"
						/>
						<span>{t(menu.key)}</span>
					</li>
				</Link>
				<Link to={choosen.path} onClick={() => setActive(3)}>
					<li key={choosen.key}>
						<div className="choosen-product-menu">
							<img
								src="/assets/images/homeplus/favourite.svg"
								alt="favourite icon"
								width="25"
								height="25"
							/>
						</div>
						<span
							className={`${active === 3 ? "text-primary" : ""}`}
						>
							{t(choosen.key)}
						</span>
					</li>
				</Link>
				<Link
					to={token ? "/checkout" : cart.path}
					onClick={() => setActive(4)}
				>
					<li key={cart.key}>
						<img
							src="/assets/images/homeplus/cart.svg"
							alt="cart icon"
							width="25"
							height="25"
						/>
						<span
							className={`${active === 4 ? "text-primary" : ""}`}
						>
							{t(cart.key)}
						</span>
					</li>
				</Link>
				<Link to={profile.path} onClick={() => setActive(5)}>
					<li key={profile.key}>
						<img
							src="/assets/images/homeplus/profile.svg"
							alt="profile icon"
							width="25"
							height="25"
						/>
						<span
							className={`${active === 5 ? "text-primary" : ""}`}
						>
							{t(profile.key)}
						</span>
					</li>
				</Link>
			</ul>
		</div>
	);
};

export default withRouter(MobileBottomNavbarThree);
